.masthead {
  height: 100vh;
  min-height: 500px;
  background-image:
  linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),
  url('../foto/endgame.png');
  background-size: cover;
  background-position: top;
  background-repeat: no-repeat;

.nav-item {
  font-family: 'Raleway', sans-serif;
}
h1 {
  font-family: 'Raleway', sans-serif;
  font-weight: bold;
  color: white;
  padding-top: 400px;
  font-size: 30pt;
}
p .lead {
  font-family: 'Roboto', sans-serif;
  color: white;
}
}
h4 {
  font-family: 'Raleway', sans-serif;
  font-weight: 800;
}

section#rezerBil .btn {
  border-radius: 50% !important;
  border-color: #f26c4f;
  width: 28px;
  height: 28px;
}
section#rezerBil input {
  width: 40px;
  height: 40px;
}
section#rezerBil img {
  max-height: 361px;
}
#slides {
  color: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4));
}
